@charset "UTF-8";
/* это бирюзовый в разных секциях: */
/* // это цвет афтера у секций: */
/* // желтый цвет ховера у меню в наве и цвет по умолчанию иконок соцсетей: */
/* // цвет соц иконок при ховере и по умолчанию для филл их же: */
/* // белый цвет всех белых элементов: */
/* // черный цвет заголовков: */
.checked {
  border: 3px dashed red;
  background-color: rgba(224, 219, 219, 0.6);
  position: relative; }

.checkedTag {
  border: 1px dashed red;
  background-color: rgba(241, 143, 143, 0.6); }

.checkedTagBem {
  border: 1px dashed red;
  background-color: rgba(228, 222, 222, 0.6); }

.header {
  margin: 0 auto;
  background: linear-gradient(360deg, rgba(252, 227, 138, 0.8), rgba(243, 129, 129, 0.8)), url("../../images/eagle@1X.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%; }
  .header__nav {
    width: 960px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 22px;
    margin-bottom: 60px; }
  .header__logoLink {
    text-decoration: none;
    align-self: flex-start; }
  .header__logo {
    fill: #ffffff; }
  .header__logo > g > text > tspan:hover {
    fill: #fce38a; }
  .header__list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 12px; }
  .header__listItem {
    box-sizing: border-box;
    padding-bottom: 10px; }
  .header__listItem:nth-child(-n+5):hover {
    border-bottom: 2px solid #fce38a;
    padding-bottom: 10px; }
  .header__listItem:hover a {
    color: #fce38a; }
  .header__listItem:nth-child(-n+5) {
    margin-right: 46px; }
  .header__listItem:nth-child(6) {
    margin-right: 32px; }
  .header__listItemLink {
    font-size: 11px;
    font-family: "Montserrat";
    font-weight: 400;
    font-style: normal;
    color: #ffffff;
    text-transform: uppercase; }
  .header__listItemCart {
    width: 17.6px;
    height: 14.03px;
    fill: #ffffff; }
  .header__listItemCart:hover {
    fill: #fce38a; }
  .header__logo {
    width: 86px;
    height: 23px;
    fill: #ffffff; }
  .header__logo:hover {
    fill: #fce38a; }
  .header__listItemGlass {
    width: 17.6px;
    height: 17.6px;
    fill: #ffffff; }
  .header__listItemGlass:hover {
    fill: #fce38a; }
  .header__info {
    padding-bottom: 90px; }
  .header__infoTitle {
    width: 412px;
    margin: 0 auto;
    font-size: 58px;
    font-family: "Kaushan Script" sans-serif;
    font-weight: 400;
    font-style: italic;
    margin-bottom: 32px;
    color: #ffffff;
    text-transform: capitalize;
    text-align: center; }
  .header__infoSubtitle {
    width: 672px;
    margin: 0 auto;
    font-size: 120px;
    font-family: "Montserrat";
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
    margin-bottom: 50px; }
  .header__infoLink {
    width: 128px;
    margin: 0 auto;
    border: 2px solid #ffffff;
    padding: 15px 32px 14px 33px;
    color: #ffffff;
    font-size: 14px;
    font-family: "Montserrat";
    font-weight: 700;
    font-style: normal;
    text-align: center;
    text-transform: uppercase; }

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .header {
    background: linear-gradient(360deg, rgba(252, 227, 138, 0.8), rgba(243, 129, 129, 0.8)), url("../../images/eagle@2X.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%; } }
