.heading {
  position: relative; }
  .heading__title--green {
    width: 260px;
    text-align: center;
    margin: 0 auto;
    font-size: 24px;
    font-family: "Montserrat";
    font-weight: 700;
    font-style: normal;
    margin-bottom: 70px;
    text-transform: uppercase;
    position: relative;
    color: #95e1d3; }
    .heading__title--green:after {
      position: absolute;
      display: block;
      content: "";
      width: 60px;
      height: 3px;
      background-color: #95e1d3;
      left: 95px;
      top: 55px; }
  .heading__title--dark {
