.checked {
  border: 3px dashed red;
  background-color: rgba(224, 219, 219, 0.6);
  position: relative; }

.checkedTag {
  border: 1px dashed red;
  background-color: rgba(241, 143, 143, 0.6); }

.checkedTagBem {
  border: 1px dashed red;
  background-color: rgba(228, 222, 222, 0.6); }

.footer {
  background-color: #95e1d3; }
  .footer__content {
    width: 960px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto; }
  .footer__license {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
    margin-bottom: 23px;
    align-items: center;
    width: 295px; }
    .footer__licenseAbout {
      font-size: 14px;
      font-family: "Montserrat";
      font-weight: 400;
      font-style: normal; }
    .footer__licenseLink {
      display: block;
      text-decoration: none;
      color: #f6a6a6;
      font-size: 14px;
      font-family: "Montserrat";
      font-weight: 400;
      font-style: normal;
      text-transform: capitalize; }
  .footer__info {
    display: flex;
    width: 376px;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 11px; }
    .footer__infoEmail {
      padding: 13px 40px 12px 13px;
      border: solid 1px #e5e5e5;
      border-right: none; }
    .footer__infoEmail::placeholder {
      font-size: 15px;
      font-family: "Roboto" sans-serif;
      font-weight: 400;
      font-style: italic;
      text-transform: capitalize; }
    .footer__infoSubscribe {
      text-transform: uppercase;
      background-color: #468d80;
      font-size: 14px;
      font-family: "Montserrat";
      font-weight: 700;
      font-style: normal;
      color: #ffffff;
      padding: 14px 33px;
      border: none; }
