.team__About {
  width: 776px;
  text-align: center;
  margin: 0 auto;
  font-size: 12px;
  font-family: "Roboto" sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #333333;
  margin-bottom: 72px; }

.team__listItemContainer {
  position: relative; }

.team__list {
  width: 960px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between; }
  .team__listItem {
    width: 304px; }
    .team__listItemName {
      font-size: 11px;
      font-family: "Montserrat";
      font-weight: 400;
      font-style: normal;
      color: #333333;
      text-align: center;
      margin-top: 24px;
      margin-bottom: 12px; }
    .team__listItemProfession {
      font-size: 12px;
