.team__popUpList {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(360deg, rgba(252, 227, 138, 0.8), rgba(243, 129, 129, 0.8));
  display: none; }
  .team__popUpListItem:nth-child(-n+3) {
    margin-right: 2px; }
  .team__popUpListLink {
    width: 56px;
    height: 56px;
    background-color: #fce38a;
    display: flex;
    justify-content: center;
    align-items: center; }
  .team__popUpListLink:hover {
    background-color: #f38181; }
  .team__popUpListLink:hover svg {
    fill: #ffffff; }
  .team__popUpListSvg {
    fill: #f38181;
    height: 26px; }
  .team__popUpListFacebook {
    width: 13px; }
  .team__popUpListTwitter {
    width: 26px;
    height: 22px; }
  .team__popUpListPinterest {
    width: 21px; }
  .team__popUpListInstagram {
    width: 26px; }

.team__listItemContainer:hover ul {
  display: flex; }

.team__listItemContainer:hover {
  box-shadow: 11px 11px 0 0 #95e1d3;
  transform: translate(-11px, -11px);
  transition-duration: 200ms;
  transition-timing-function: linear; }
